.featured-pool .switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.featured-pool .switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.featured-pool .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #17434F;
  transition: .4s;
}

.featured-pool .slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: .4s;
}

.featured-pool input:checked + .slider {
  background-color: #17434F;
}

.featured-pool input:focus + .slider {
  box-shadow: 0 0 1px #17434F;
}

.featured-pool input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.featured-pool .slider.round {
  border-radius: 34px;
}

.featured-pool .slider.round:before {
  border-radius: 50%;
}
.featured-pool .main-slider{
  display: flex;
