.bar {
  display: block;
  width: 25px;
  height: 3px;
  margin: 5px auto;
  border-radius: 50px;
  transition: all 0.3s ease-in-out;
  background-color: #ffffff;
}
.navbar .navbar-toggler.collapsed .bar:nth-child(2) {
  opacity: unset;
}

.navbar .navbar-toggler.collapsed .bar:nth-child(1) {
  -webkit-transform: unset;
          transform: unset;
}

.navbar .navbar-toggler.collapsed .bar:nth-child(3) {
  -webkit-transform: unset;
          transform: unset;
}
.navbar .navbar-toggler .bar:nth-child(2) {
  opacity: 0;
}

.navbar .navbar-toggler .bar:nth-child(1) {
  -webkit-transform: translateY(8px) rotate(45deg);
          transform: translateY(8px) rotate(45deg);
}

.navbar .navbar-toggler .bar:nth-child(3) {
  -webkit-transform: translateY(-8px) rotate(-45deg);
